@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,400&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Montserrat", sans-serif !important;
}

:root {
  --white: #f8f8f8;
  --light: #f8f8f8;
  --head-light: #f8f8f8;
  --grey: #232323;
  --gradient-grey: #232323;
  --skill-grey: #272727;
  --theme: #8148eb;
  --dark: #191919;
  --purple: #8e61e2;
  --logo-bg: none;
  --light-purple: #bd6df2;
  --shadow: none;
  --icon-bg: #5555556e;
  --gradient: linear-gradient(134deg, #836af0 0%, #8e49dc 99%);
  --gradient-light: linear-gradient(99deg, #836af0 0%, #8e49dc 130%);
  --input: #f0f0f0;
  --filter: brightness(20%);
}

.light {
  --light: #8949dc;
  --head-light: #313131;
  --grey: #f8f8f8;
  --gradient-grey: var(--gradient);
  --skill-grey: #dfc7ff;
  --dark: #f0ecec;
  --logo-bg: #8e61e2;
  --light-purple: #f8f8f8;
  --shadow: -1px 3px 12px 0px #8b8b8b21;
  --icon-bg: rgba(209, 209, 209, 0.233);
  --input: #eeeeee;
  --filter: contrast(15%);
}

#root {
  color: #f8f8f8;
  color: var(--light);
  background: #191919;
  background: var(--dark);
  min-height: 100vh;
  transition: 300ms ease-in-out;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 10px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #191919;
  background: var(--dark)
}

::-webkit-scrollbar-thumb {
  background: #8e61e2;
  background: var(--purple);
  border-radius: 5px;
}

.navbar {
    background: var(--grey);
    overflow: hidden;
    height: 39px;
    width: 100%;
    padding: 1rem 0;
    z-index: 6;
}

.modeToggle {
    cursor: pointer;
    color: var(--white);
    background: var(--gradient);
    margin: 5px 20px;
    float: right;
    border-radius: 50%;
    font-size: 1.2em;
    font-weight: 900;
    transition: 200ms ease-in-out;
    z-index: 10;
    position: absolute;
    right: 0;
    top: 17px;
}

.modeToggle i {
    padding: 5px;
}

.modeToggle:hover {
    transform: scale(1.12);
}

.logo {
    color: var(--logo-bg);
    font-size: 2rem;
    float: left;
    margin: auto 30px;
}

.logo_m {
    color: var(--logo-bg);
    font-size: 2rem;
    float: right;
    margin: auto 60px;
}

.logo a {
    text-decoration: none;
    color: var(--logo-bg);
}

.hamburger {
    display: none;
}

@media screen and (max-width: 768px) {
    .navbar {
        min-width: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    .logo {
        font-size: 1.4rem;
    }
    .line-1,
    .line-2,
    .line-3 {
        width: 30px;
        height: 3px;
        background: var(--purple);
        margin: 5px;
        transition: 400ms;
        border-radius: 10px;
    }
    .hamburger {
        display: block;
        position: fixed;
        cursor: pointer;
        left: 10px;
        top: 20px;
        z-index: 20;
    }
}
.App {
  text-align: center;
}

.message {
    padding: 10px;
    margin: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: left;
    max-width: 60%;
}

.messageHeader {
    font-size: 12px;
    color: #555555;
}

.message .avatar {
    width: 30px;
    position: absolute;
    top: -20px;
    left: -10px;
    transform: unset;
}

.messageUser {
    margin-left: auto;
    color: var(--white);
}

.userCard {
    background-color:var(--purple);
    position: relative;
    margin-left: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 15px;
}

.otherCard {
    background-color: var(--grey);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    border-radius: 15px;
}

.messageText {
    padding: 15px;
}
.rooms {
    display: flex;
    flex-direction: column;
    flex: 0.35 1;
    max-width: 400px;
    min-width: 250px;
    background-color: var(--grey);
    height: calc(100vh - 131px);
    overflow: hidden;
    overflow-y: auto;
    transition: width 700ms, max-width 700ms, min-width 700ms;
}

.menu {
    width: calc(100% - 40px);
    padding: 20px;
    text-align: center;
    display: flex;
    transition: all .3s;
    border-bottom: 1px solid var(--dark);
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.1);
}

.menu:hover {
    transform: scale(1.1);
}

.logout {
    cursor: pointer;
    color: var(--white);
    font-size: 15px;
    background: var(--gradient);
    border: none;
    padding: 5px 10px;
    float: right;
    border-radius: 25px;
    font-size: 1.2em;
    transition: 200ms ease-in-out;
}

.logout:hover {
    text-decoration: underline;
    cursor: pointer;
}

.placeholder {
    width: calc(100% - 40px);
    padding-top: 90px;
    display: block;
}

.close,
.open {
    max-width: 400px;
    min-width: 250px;
}

@media screen and (max-width: 768px) {
    .close {
        width: 0px;
        max-width: 0px;
        min-width: 0px;
    }
}
.room {
    width: calc(100% - 40px);
    padding: 20px;
    text-align: left;
    display: flex;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    border-bottom: 1px solid var(--dark);
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.1);
}

.room:hover {
    background-color: var(--dark);
}

.roomInfo {
    margin-left: 20px;
}

.roomInfo h2{
    margin: 0;
    font-size: 20px;
}


.room .avatar {
    height: 40px;
    transform: unset;
}

.roomInfo p {
    font-weight: 400;
    width: 150px;
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0;
}

a {
    text-decoration: none !important;
    color: var(--light);
}
.chat {
	flex: 1 1;
	height: calc(100vh - 218px);
	position: relative;
	overflow: hidden;
    overflow-y: auto;
}

.content {
	display: flex;
}

.welcome {
	margin: auto;
	padding: 10px;
}


.form {
	text-align: center;
	background-color: var(--grey);
	position: fixed;
	padding: 20px;
	bottom: 60px;
	left: 0;
	z-index: 1;
	width: calc(100% - 40px);
}

.avatar {
	transform: translateY(10px);
	width: 40px;
	border-radius: 50%;
}

.formcontrol {
	display: flex;
	flex-direction: row;
}

.formmessage {
	flex: 1 1;
	min-width: 100px;
	font-family: "Montserrat", sans-serif;
	border: none;
	padding: 15px;
	border-radius: 50px;
}

.formmessage:focus, .formbutton:focus {
	outline: none;
}

.formbutton {
	flex: 0 1;
	color: var(--purple);
	padding: 10px;
	margin-left: 10px;
	border: none;
	border-radius: 50%;
	cursor: pointer;
	background: transparent;
}

.formbutton:disabled {
	cursor: not-allowed;
}

.formbutton i {
	font-size: 25px;
}


@media screen and (max-width: 768px) {
	.messages {
		min-height: calc(100vh - 323px);
	}
}
.login {
	height: calc(100vh - 71px);
	background-color: var(--dark);
	display: grid;
	place-items: center;
}

.loginContainer {
    max-width: 400px;
	padding: 20px;
	text-align: center;
	background-color: var(--grey);
	border-radius: 15px;
	box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}

.loginContainer button {
    border: none;
    width: 100%;
    margin: 10px 0;
	border-radius: 40px;
	background-color: var(--purple);
	padding: 10px;
	font-size: 18px;
	color: var(--white);
	cursor: pointer;
}

.loginContainer .register {
    background-color: var(--input);
    color: var(--theme);
}

.loginContainer > form > h5 {
    margin: 5px;
    text-align: left;
}

.loginContainer > form > input {
	height: 30px;
    margin-bottom: 10px;
    padding: 5px;
    border: 0px;
	background-color: white;
	width: calc(100% - 10px);
}

.loginContainer > p {
	margin-top: 15px;
	font-size: 12px;
}
.footer {
    height: 60px;
    width: 100%;
    position: fixed;
    background: var(--grey);
    bottom: 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    z-index: 6;
}

.footer p {
    line-height: 24px;
    font-size: 16px;
    padding: 0;
    color: var(--head-light);
    text-align: center;
}

.footer a {
    color: var(--purple);
    text-decoration: none;
}
